const RenderFullPageInner = ({
  next,
  previous,
}: {
  next: (() => void) | undefined;
  previous: (() => void) | undefined;
}) => {
  const { epochDetails } = UseContractState.useContainer();

  return (
    <>
      <div className="section">
        <Page>
          <Landing next={next} />
        </Page>
      </div>
      {epochDetails
        .reverse()
        .map((epoch, i) => (
          <div className="section">
            <Page
              style={{
                background: epoch.light ? "#fff" : "#000",
                ...(epoch.light ? { color: "#000" } : {}),
              }}
              data-anchor={`epoch-${i}`}
            >
              <ShowEpoch
                i={i}
                epoch={epoch}
                previous={previous}
                next={i == 1 ? undefined : next}
              />
            </Page>
          </div>
        ))
        .valueSeq()
        .toArray()}
    </>
  );
};

// TODO: Come up with a better solution than a global variable.
const RenderFullpage = ({ state, fullpageApi }: any) => {
  if (fullpageApi) {
    setTimeout(() => {
      fullpageApi.setScrollingSpeed(1000);
    }, 1000);
  }

  const next = fullpageApi
    ? () => {
        fullpageApi.moveSectionDown();
      }
    : undefined;

  const previous = fullpageApi
    ? () => {
        fullpageApi.moveSectionUp();
      }
    : undefined;

  return (
    <ReactFullpage.Wrapper>
      <RenderFullPageInner next={next} previous={previous} />
    </ReactFullpage.Wrapper>
  );
};

const isTouchDevice = () => {
  return (
    "ontouchstart" in window ||
    navigator.maxTouchPoints > 0 ||
    navigator.msMaxTouchPoints > 0
  );
};

